.categoryContainer{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding:0 22px;
    background-color: #fff;
    height:160px;
    overflow:hidden;
    width: 100%;

    
}
.productDiv{
    height: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap:8px;
}
.productDiv p{
    font-weight: bold;
    
}
.imageDiv{
    height: 100px;
    width:100px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 6px;
}
.imageDiv img{
    height: 80px;
}

.titleContainer{
    display: flex;
    align-items: center;
}

@media(max-width:1000px){
    .productDiv p{
        font-weight: normal;
        font-size: 14px;
    }
    .imageDiv img{height:60px;}
    .productDiv{height:100px;}
    .categoryContainer{overflow:scroll;}
}

